#chatview-container{
  background-color: #fff;
  position: fixed;
  top: 35px;
  left: 0;
  height: calc(100% - 80px);
  overflow: auto;
  padding: 0 25px 60px;
  // margin-left: 300px;
  box-sizing: border-box;
  overflow-y: scroll;
  width: 100%;
  max-width: 100%;
  margin: 0 !important;
}
.chatBack{
  width: 35px;
  height: 35px;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  text-align: center;
  svg{
    width: 18px;
    height: 35px;
    path{
      fill: #fff;
    }
  }
}
#chatHeader{
  width: 100%;
  height: 35px;
  background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
  position: fixed;
  top: 0;
  left: 0;
  font-size: 15px;
  line-height: 35px;
  text-align: center;
  color: white;
  box-sizing: border-box;
}
#userSent{
  float: left;
  clear: both;
  padding: 10px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-top: 10px;
  background-color: rgb(110, 110, 110);
  color: white;
  width: 300px;
  border-radius: 10px;
}
#friendSent{
  float: right;
  clear: both;
  padding: 10px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-top: 10px;
  background-color: rgb(70, 70, 70);
  color: white;
  width: 300px;
  border-radius: 10px;
}
